.tableFunc {
  height: 2rem;
  display: flex;
  justify-content: space-between;

  .search {
    padding: 0.5rem 0.75rem;
    height: 100%;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.875rem;
    font-weight: 400;
    border: 1px solid #d9d9d9;
    border-radius: 0.25rem;

    input {
      outline: none;
      border: none;
      color: #999999;
    }
  }

  .buttons {
    height: 100%;
    display: flex;
    gap: 0.75rem;

    div > button {
      height: 100%;
      width: auto;
      background: #ffffff;
      color: #4d4d4d;
      border: 1px solid #d9d9d9;
      border-radius: 0.25rem;
      cursor: pointer;

      img {
        height: 0.75rem;
        width: 0.75rem;
      }
    }

    :nth-child(1) button {
      padding: 0.375rem 0.75rem;
      display: flex;
      gap: 0.375rem;
      font-size: 1rem;
      font-weight: 400;
      align-items: center;
    }

    :nth-child(2) button {
      padding: 0.5rem;
    }
  }
}

.tableHeader {
  height: 2rem;
  background: #f2f2f2;
  display: flex;
  border-radius: 0.25rem;
  padding: 0.25rem 0.5rem;
  justify-content: space-between;
  align-items: center;

  div {
    font-size: 0.875rem;
    font-weight: 500;
    color: #4d4d4d;
    display: flex;
    align-items: center;
    gap: 0.25rem;

    img {
      color: #4d4d4d;
      height: 0.375rem;
      width: 0.375rem;
      margin-top: 0.2rem;
    }
  }

  :nth-child(4) {
    img {
      width: 0.75rem;
      height: 0.75rem;
      margin-top: 0.1rem;
    }
  }
}
